@import url("./styles.css");
/* Plain .css files are supported */
@import url("./styles.less");
/* .less files are also supported */
/*
 * But usually, it's a good idea to use .scss files to generate CSS styles
 */
* {
  color: #00f; }

/* The animation code */
@-webkit-keyframes redtransition {
  0% {
    color: blue; }
  100% {
    color: red; } }

@keyframes redtransition {
  0% {
    color: blue; }
  100% {
    color: red; } }

/* The element to apply the animation to */
.red {
  -webkit-animation-name: redtransition;
  animation-name: redtransition;
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-delay: 1s;
  animation-delay: 1s;
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards; }



